
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Dosis', sans-serif;
}
::-moz-selection {
  color: white;
  background: #f44336;
}
::selection {
  color: white;
  background: #f44336;
}
.navigation {
  height: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding: 0 30px;
  width: 100%;
  position: absolute;
}
.navigation a {
  display: -ms-flexbox;
  display: flex;
  opacity: .3;
  transition: .2s;
  color: white;
}
.navigation a:not(:last-child) {
  margin-right: 20px;
}
.navigation a:focus {
  outline: none;
}
.navigation a:focus svg {
  box-shadow: 0 0 0 6px rgba(255, 255, 255, 0.2);
}
.navigation a:hover,
.navigation a:focus {
  opacity: 1;
}
.navigation a svg {
  height: 32px;
  border-radius: 50%;
  box-shadow: 0;
  fill: #f0f0f0;
  transition: .2s;
}
.header {
  background: #272727;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 600px;
  height: 100vh;
  color: #f0f0f0;
}
.header p {
  font-size: 1.2em;
}
.header h1 {
  font-size: 2.75em;
  font-weight: normal;
  margin: 0;
}
.container {
  max-width: 1180px;
  margin: 0 auto;
}
.section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.section__title {
  margin: 40px 0 30px;
  font-weight: normal;
}
.skills {
  background: #f44336;
}
.skills__title {
  color: white;
}
.skills__content {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[4];
      grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 200px;
  width: 100%;
  background: white;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  border-radius: 3px;
  margin-bottom: 80px;
  overflow: hidden;
}
.skills__item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  transition: .3s;
}
.skills__item:hover {
  background: #f0f0f0;
}
.skills__item:hover svg {
  fill: #272727;
}
.skills__item svg {
  height: 90px;
  fill: #ccc;
  transition: .6s;
}
.skills__coming {
  font-weight: bold;
  color: #ccc;
  text-align: center;
  padding: 5px;
}
.skills__coming:hover {
  background: none;
}
.footer {
  background: #272727;
}
.footer__content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #ccc;
}
@media screen and (max-width: 1240px) {
  .skills__content {
    width: 90%;
  }
}
@media screen and (max-width: 900px) {
  .skills__content {
    -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
    width: 90%;
  }
}
@media screen and (max-width: 630px) {
  .skills__content {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 100px;
    width: 90%;
  }
  .skills__item svg {
    height: 50px;
  }
}
